@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&display=swap);
.board{display:flex;flex-direction:column;align-items:center}.board .board-wrapper{opacity:.4;counter-reset:row column}.board .board-wrapper .board-row{counter-increment:row}.board .board-wrapper .board-row:last-child .board-element{counter-increment:column}.board .board-wrapper .board-row:last-child .board-element::after{content:counter(column, upper-latin)}.board .board-wrapper .board-row::before{content:counter(row)}.board .board-wrapper .board-row .board-element:first{background-color:red}.board .board-wrapper .board-row .board-element .board-tile{position:relative;width:calc(1rem + 1vw);height:calc(1rem + 1vw);background-color:#eeeeee;border:1px solid #393b44}.board .board-wrapper .board-row .board-element .ship-not-hit{background-color:#839b97;cursor:pointer}.board .board-wrapper .board-row .board-element .ship-hit{background-color:#c86b85}.board .board-wrapper .board-row .board-element .ship-hit::after{content:'\f00d';position:absolute;font-family:'Font Awesome 5 Free';font-weight:900;font-size:10px;left:50%;top:50%;transform:translate(-50%, -50%)}.board .board-wrapper .board-row .board-element .ship-sunk{background-color:#bbbbbb}.board .board-wrapper .board-row .board-element .ship-sunk::after{content:'\f00d';position:absolute;font-family:'Font Awesome 5 Free';font-weight:900;font-size:10px;left:50%;top:50%;transform:translate(-50%, -50%)}.board .board-wrapper .board-row .board-element .missed{background-color:#bce6eb}.board .board-wrapper .board-row .board-element .missed::after{content:'\f111';position:absolute;font-family:'Font Awesome 5 Free';font-weight:900;font-size:5px;left:50%;top:50%;transform:translate(-50%, -50%)}.board .board-wrapper .board-row .board-element .marked{background-color:#9f5f80}.board .board-wrapper .board-row .board-element .marked-origin{background-color:#9f5f80}.board .board-wrapper .board-row .board-element .marked-origin::after{content:'\f0e2';position:absolute;font-family:'Font Awesome 5 Free';font-weight:900;font-size:10px;left:50%;top:50%;transform:translate(-50%, -50%)}.board .board-wrapper .board-row .board-element .valid{background-color:#8db596}.board .board-wrapper .board-row .board-element .valid-origin{background-color:#8db596}.board .active{opacity:.8}.board .active .board-row .board-element .board-tile:hover{border:1px solid #8db596;cursor:pointer}.board h4{margin-top:1rem;font-weight:300;font-size:1rem}

.ships-container{position:absolute;display:flex;flex-direction:column;grid-gap:0.5rem;gap:0.5rem;top:1rem}.ships-container .ship-wrapper{display:flex;grid-gap:0.2rem;gap:0.2rem}.ships-container .ship-wrapper .part{width:0.5rem;height:0.5rem;background-color:#839b97}.ships-container .ship-wrapper .sunk{background-color:#bbbbbb}.player{align-items:flex-end;right:95%}.computer{left:95%}@media all and (max-width: 500px){.ships-container{display:none}}

.boards-container{width:100vw;height:60vh;display:flex;flex-wrap:wrap;justify-content:center;align-items:center;grid-gap:2rem;gap:2rem}.boards-container .board-container{display:flex;padding:0 4rem 0 4rem;grid-gap:0.5rem;gap:0.5rem;position:relative}

.app .display-wrapper{width:100vw;height:20vh;display:grid;place-items:center}.app .display-wrapper .display{height:4rem;width:20rem;display:grid;place-items:center;border:1px solid #333333;border-radius:0.2rem;background-color:#f4f4f4}.app .display-wrapper .display h2{font-size:2rem;font-weight:300}.app .buttons{width:100vw;height:10vh;display:grid;place-items:center}.app .buttons button{width:8rem;height:1.5rem;cursor:pointer}

*{margin:0;padding:0;box-sizing:border-box}body{font-size:62.5%;font-family:'Montserrat', sans-serif;background-color:#eeeeee;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

